.about-top{
  padding: 1em 0;
  min-height: 25em;
  background-size: cover;
  background-position: center;
  position: relative;
  &:after{
    background: rgba($black,.4);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  .breadcrumbs__item{
    color: $white;
  }
  &__text{
    color: $white;
    @include centerAbsolute;
    z-index: 2;
    width: 100%;
    text-align: center;
  }
}

.about{
  padding: 4em 0 2em;
  &__items,
  .text{
    max-width: 64.875em;
  }
  &__items{
    margin-top: 4em;
    padding-top: 4em;
    border-top: $border;
  }
}

.about-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1em;
  text-align: center;
  &__img{
    height: 6.5em;
    margin-bottom: 1em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 137%;
    font-weight: 600;
  }
}


@media screen and (max-width: 992px){
  .about-top{
    padding: .75em 0;
    min-height: 20em;
  }

  .about{
    padding: 1em 0 .75em;
    &__items{
      margin-top: 2em;
      padding-top: 2em;
    }
  }

  .about-item{
    padding: 0;
    &__img{
      height: 4.75em;
    }
    &__text{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 576px){
  .about-top{
    min-height: 11.25em;
  }

  .about{
    &__items{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .about-item{
    flex-direction: row;
    text-align: left;
    &__img{
      flex-shrink: 0;
      height: unset;
      width: 4.75em;
      margin: 0 1em 0 0;
      img{
        width: 100%;
        height: auto;
      }
    }
  }
}
