.grid,
.grid2,
.grid3,
.grid4,
.grid5{
  display: grid;
  gap: 1.5em;
}

.grid{
  grid-template-columns: 100%;
}

.grid2{
  grid-template-columns: repeat(2,calc(50% - .75em));
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
}

.grid4{
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
}

.grid5{
  grid-template-columns: repeat(5,calc(20% - 6em / 5));
}


@media screen and (max-width: 992px){
  .grid-tablet3,
  .grid-tablet4{
    gap: .75em;
  }

  .grid-tablet{
    grid-template-columns: 100%;
  }

  .grid-tablet2{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }

  .grid-tablet3{
    grid-template-columns: repeat(3,calc(100% / 3 - .5em));
  }

  .grid-tablet4{
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
  }
}


@media screen and (max-width: 576px){
  .grid-mob{
    grid-template-columns: 100%;
  }

  .grid-mob2{
    grid-template-columns: repeat(2,calc(50% - .375em));
  }
}
