.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.5em;
    padding: 0 .5em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $input-fields;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: .125em;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary;
      width: 1em;
      height: 1em;
      top: -.4375em !important;
      right: -.4375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 150%;
        height: 150%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 .75em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-47%);
    }
  }
  .fg{
    position: relative;
    input{
      height: 2.86em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 143%;
      padding-left: 2.65em;
      border-radius: .57em;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}
