.favourite{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__delete{
    display: flex;
    align-items: center;
    color: $light-text;
    &:hover{
      color: $red;
      path{
        stroke: $red;
      }
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
    svg{
      width: 1.5em;
      margin-left: .75em;
    }
  }
  &__grid{
    margin-top: 2.5em;
  }
}

.favourite-empty{
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__text{
    font-size: 1em;
    line-height: 137%;
  }
  &__nav{
    margin: 2em auto 0;
    width: 33.125em;
    max-width: 100%;
  }
  &__title{
    font-size: 1.375em;
    line-height: 136%;
    font-weight: 600;
    margin-bottom: .72em;
  }
}


@media screen and (max-width: 992px){
  .favourite{
    &__grid{
      margin-top: 1.5em;
    }
  }

  .favourite-empty{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .favourite{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__delete{
      margin-top: 1.5em;
    }
  }

  .favourite-empty{
    &__text{
      font-size: .875em;
      line-height: 143%;
    }
    &__nav{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__btn{
      span{
        font-size: .875em;
      }
    }
    &__title{
      font-size: 1.125em;
      line-height: 144%;
      margin-bottom: .44em;
    }
  }
}
