.swiper-button{
  width: 3em;
  height: 3em;
  @include center;
  border-radius: 50%;
  box-shadow: $shadow;
  background-color: $white;
  top: 50%;
  margin: 0;
  transform: translateY(-50%);
  img{
    width: .5625em;
  }
  &:hover{
    path{
      stroke: $primary-hover;
    }
  }
  &-prev{
    left: -1.5em;
  }
  &-next{
    right: -1.5em;
  }
}

.swiper-pagination{
  width: 100%;
  @include center;
  z-index: 3;
  &-bullet{
    width: .75em;
    height: .75em;
    opacity: 1;
    border: 2px solid $white;
    background-color: transparent;
    margin: 0 .375em !important;
    &-active{
      background-color: #fff;
    }
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
}

.main{
  padding: 2.75em 0 2em;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      bottom: 1.5em;
    }
  }
}

.main-banner{
  padding: 6.25em 7.5em;
  min-height: 30em;
  border-radius: 1.5em;
  background-size: cover;
  background-position: center;
  &__content{
    max-width: 48.125em;
    position: relative;
    z-index: 2;
  }
  &__title{
    font-size: 2.75em;
    line-height: 155%;
    font-weight: 800;
    color: $white;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;
    display: inline;
    position: relative;
    padding: .125em .375em;
    background: linear-gradient(313deg, #ca0041 0%, #662082 100%);
  }
}

.categories{
  padding: 2em 0;
}

.category-card{
  border: $border;
  border-radius: 1em;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &:hover{
    box-shadow: $shadow;
    color: $black;
  }
  &__img{
    @include imgCover(61.5%);
  }
  &__text{
    flex-grow: 1;
    padding: .75em 1em 1em;
    text-align: center;
    @include center;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: .75em 0 .5em;
  }

  .main-banner{
    border-radius: 1em;
    padding: 3.5em;
    min-height: 24em;
    &__title{
      font-size: 2.25em;
    }
  }

  .swiper-button{
    width: 2em;
    height: 2em;
    svg{
      width: .375em;
    }
    &-prev{
      left: -1em;
    }
    &-next{
      right: -1em;
    }
  }

  .categories{
    padding: .5em 0 .75em;
  }

  .category-card{
    &__text{
      padding: .5em .625em;
      min-height: 3.375em;
    }
    &__title{
      font-size: .8125em;
      line-height: 138%;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    .swiper{
      &-pagination{
        bottom: .5em;
        &-bullet{
          width: .5em;
          height: .5em;
          margin: 0 .25em !important;
        }
      }
    }
  }

  .main-banner{
    padding: 1.5em;
    min-height: 11.75em;
    &__content{
      max-width: 16em;
    }
    &__title{
      font-size: .875em;
    }
  }
}
