.catalog{
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: flex-end;
  }
  &__count{
    padding: 0 0 .5em 1em;
    color: $light-text;
    font-weight: 500;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 17.125em calc(100% - 22.125em);
    gap: 0 5em;
  }
}

.m-select{
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.5em;
    white-space: nowrap;
    height: 2.5em;
    padding: 0 0 0 1em;
    width: 100%;
    border: 1px solid $input-fields;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    .current{
      font-size: .875em;
    }
    &.open{
      &:before{
        transform: rotate(-180deg);
      }
    }
    &:focus{
      border: 1px solid #aeaeb6;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: 2.5em;
      height: 2.5em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(1em);
      transition: all .3s;
    }
    .list{
      margin-top: 1px;
      border-radius: .5em;
      width: calc(100% + 2px);
      left: -1px;
      border: none;
      background: #FFF;
      box-shadow: $shadow;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 2.86em;
      font-size: .875em;
      line-height: 143%;
      padding: .5em 2.28em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      background-image: url(../img/check.svg);
      background-repeat: no-repeat;
      background-size: 0 auto;
      background-position: left .85em center;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover{
        background-color: $light-grey;
      }
      &.selected{
        background-size: .85em auto;
        font-weight: 400;
        background-color: transparent !important;
      }
      &.focus,
      &.selected.focus{
        //background-color: $primary;
        //border-color: $primary;
        //font-weight: 400;
        //color: #fff;
      }
    }
  }
}

.sort{
  display: flex;
  align-items: center;
  label{
    font-size: .875em;
    line-height: 143%;
    font-weight: 600;
    margin-right: 1.14em;
  }
  .nice-select{
    min-width: 15em;
  }
}

.filter-btn{
  display: none;
}

.page-nav{
  margin-top: 2.5em;
  &__more{
    text-transform: uppercase;
    color: $primary;
    width: 100%;
    border: $border;
    span{
      font-size: .875em;
    }
    &:hover{
      border-color: $primary;
      box-shadow: $shadow;
    }
  }
  &__bottom{
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__top{
      display: block;
    }
    &__info{
      display: block;
    }
    &__count{
      padding: .25em 0 0;
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: 100%;
    }
  }

  .sort{
    margin: .75em 0 0;
    label{
      display: none;
    }
  }

  .filter-btn{
    display: flex;
    width: 100%;
    margin: 1em 0 0;
    img{
      width: 1.5em;
      margin-right: 1em;
    }
    span{
      font-size: .875em;
    }
  }

  .page-nav{
    margin-top: 1em;
    &__more{
      height: 2.75em;
    }
    &__bottom{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__count{
      span{
        font-size: .75em;
      }
    }
  }
}
