.footer{
  background-color: $footer;
  color: $white;
  margin-top: 4.25em;
  &__logo{
    display: block;
    width: 7.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__conf,
  &__c{
    font-size: .875em;
    line-height: 143%;
    color: rgba($white,.8);
  }
  &__conf{
    text-decoration: underline;
  }
}

.footer-top{
  padding: 3em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-group{
  &--company{
    max-width: 13.375em;
  }
  &__top{
    border-bottom: 1px solid rgba($white,.12);
    padding-bottom: 1em;
    margin-bottom: 1em;
  }
  &__title{
    font-size: 1em;
    line-height: 137%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__links{
    @media screen and (min-width: 577px){
      &._col2{
        ul{
          column-count: 2;
          column-gap: 1.5em;
          max-width: 28em;
        }
      }
    }
    li{
      margin-bottom: 1em;
    }
    a{
      display: inline-block;
      width: 100%;
      font-weight: 500;
      font-size: 1em;
      line-height: 137%;
      color: rgba($white,.8);
    }
  }
}

.footer-contacts{
  width: 20.625em;
  max-width: 100%;
  &__street{
    max-width: 17.375em;
    font-size: 1em;
    line-height: 137%;
    font-weight: 500;
    opacity: .8;
  }
}

.footer-socials{
  margin-top: 1em;
  display: flex;
  &__item{
    margin-right: .75em;
    width: 2.5em;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
    &:hover{
      rect{
        fill-opacity: .25;
      }
    }
  }
}

.footer-bottom{
  padding: 1.5em 0;
  background: #442351;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 1.25em;
    &__logo{
      grid-column: 1/-1;
    }
  }

  .footer-top{
    padding: 1.5em 0 1.25em;
    &__inner{
      @include grid100gap(2em);
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(2,calc(50% - 1em));
        .footer-group{
          &--catalog{
            grid-column: 1/-1;
          }
          &__links._col2{
            max-width: 100%;
            column-gap: 2em;
          }
        }
      }
    }
  }

  .footer-bottom{
    padding: 1.125em 0;
  }

  .footer-group{
    &__links._col2 ul{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    position: relative;
    &__logo{
      position: absolute;
      width: 4em;
      bottom: 1em;
      left: 1.125em;
    }
    &__conf,
    &__c{
      font-size: .75em;
      line-height: 150%;
    }
    &__c{
      margin-bottom: .33em;
    }
  }

  .footer-group{
    &__top{
      padding-bottom: .75em;
    }
    &__title,
    &__links a{
      font-size: .875em;
      line-height: 143%;
    }
    &__links li{
      margin-bottom: 1.14em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }

  .footer-contacts{
    &__street{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .footer-bottom{
    &__inner{
      flex-direction: column;
      align-items: flex-start;
      padding-left: 5.25em;
    }
  }
}
