.text{
  h2{
    font-size: 1.75em;
    line-height: 136%;
    font-weight: 700;
    margin-bottom: .86em;
  }
  h3{
    font-size: 1.375em;
    line-height: 136%;
    font-weight: 600;
    margin-bottom: .72em;
  }
  p{
    font-size: 1em;
    line-height: 137%;
    @include lastM(0 0 1em);
  }
  hr{
    height: 1em;
    display: block;
    opacity: 0;
    margin: 0;
  }
}

@media screen and (max-width: 992px){
  .text{
    h2{
      font-size: 1.375em;
      margin-bottom: .72em;
    }
    h3{
      font-size: 1.125em;
      line-height: 144%;
      margin-bottom: .67em;
    }
    p{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


