.filter{
  &__top,
  &__bottom,
  &__bg{
    display: none;
  }
}

.filter-group{
  padding-bottom: 1.5em;
  margin-bottom: 1.5em;
  border-bottom: $border;
  &:last-child{
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  &._open{
    .filter-group__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 143%;
    font-weight: 600;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
    }
  }
  &__bottom{
    padding-top: 1em;
    display: none;
  }
  &__checks{
    @include grid100gap(.75em);
  }
}

.filter-available{
  display: flex;
  align-items: center;
  justify-content: space-between;
}


@media screen and (max-width: 992px){
  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    z-index: 100;
    transition: all .5s;
    transform: translateX(-120%);
    max-width: 20em;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      z-index: 99;
      background: rgba(0, 0, 0, 0.6);
    }
    &__top{
      height: 4.375em;
      @include center;
      position: relative;
    }
    &__title{
      text-align: center;
      font-size: 1.125em;
      font-weight: 700;
    }
    &__close{
      position: absolute;
      width: 2em;
      top: .5em;
      right: .5em;
      img{
        width: 100%;
        display: block;
      }
    }
    &__bottom{
      padding: .75em 1em;
      display: flex;
      box-shadow: $shadowMob;
    }
    &__btn{
      width: 100%;
      height: 2.75em;
      span{
        font-size: .875em;
      }
    }
    &__content{
      overflow-y: auto;
      overflow-x: hidden;
      height: calc(100 * var(--vh) - 8.625em);
      padding: 0 1.125em 1.5em;
    }
  }
}


