.page-top{
    padding: 1em 0 2em;
}

.breadcrumbs {
    &__item {
        position: relative;
        display: inline;
        font-size: .875em;
        line-height: 143%;
        letter-spacing: .01em;
        color: $light-text;
        &:not(:first-child) {
            //margin-left: .75em;
            &:before {
                content: '/';
                margin: .125em .125em 0 -.125em;
                //position: absolute;
                z-index: 1;
                //top: -.03125em;
                //right: 100%;
                //margin-right: .25em;
            }
        }
        &:last-child{
            font-weight: 400;
            pointer-events: none;
        }
        &:hover{
            //color: $darkBlue;
        }
    }
}



@media screen and (max-width: 992px){
    .page-top{
        padding: .75em 0 1em;
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        &__item{
            font-size: .75em;
            line-height: 133%;
        }
    }
}
