.catalog-item{
  padding-bottom: 2em;
  &__top{
    @include grid100gap(.75em);
  }
  &__code{
    font-size: .875em;
    line-height: 143%;
    color: $light-text;
  }
  &__grid{
    display: grid;
    grid-template-columns: 40.5em calc(100% - 44.25em);
    gap: 3.75em;
    margin-top: 2.5em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 5.875em calc(100% - 5.875em - 1.5em);
  gap: 1.5em;
  &__img{
    display: block;
    @include imgCover(76.2%);
    padding: 0;
    height: 100%;
    transition: all .3s;
    img{
      transition: all .3s;
    }
  }
  &__small{
    position: relative;
    height: 32.375em;
    &-wrap{
      height: 100%;
      overflow: hidden;
    }
    .swiper{
      height: calc(100% + .75em);
      &-slide{
        height: 20%;
        padding-bottom: .75em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &-thumb-active .gallery__img{
          &:before{
            border-width: 2px;
            border-color: $primary;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      border: none;
      border-radius: .5em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border: $border;
      }
    }
  }
  &__big{
    width: 100%;
    height: 32.375em;
    position: relative;
    border: $border;
    border-radius: 1em;
    .swiper{
      height: 100%;
    }
  }
  .swiper-button{
    width: 2em;
    height: 2em;
    svg{
      width: .375em;
    }
    @media screen and (min-width: 993px){
      left: 50%;
      transform: translateX(-50%);
      right: unset;
      &-prev{
        top: -1em;
        svg{
          transform: rotate(90deg);
        }
      }
      &-next{
        top: unset;
        bottom: -1em;
        svg{
          transform: rotate(90deg);
        }
      }
    }
  }
}

.catalog-info{
  display: grid;
  grid-template-columns: 17.125em calc(100% - 22.125em);
  gap: 2em 5em;
}

.catalog-props{
  padding-top: 1.5em;
  max-width: 10.75em;
  .props{
    margin-top: 1em;
  }
}

.props{
  @include grid100gap(.75em);
}

.prop-item{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  font-weight: 500;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: 2px;
    border-top: 1px dashed $line;
    margin: 0 .5em .25em;
  }
  &__desc{
    order: 2;
  }
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 143%;
  }
}

.catalog-colors{
  margin-top: 2em;
  &__items{
    margin-top: 1em;
    @include grid100gap(.75em)
  }
}

.catalog-cart{
  padding: 1.5em;
  box-shadow: $shadow;
  border-radius: 1em;
  position: sticky;
  top: 1em;
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__count-title,
  &__title{
    font-size: .875em;
    line-height: 143%;
    font-weight: 600;
  }
  &__title{
    padding: 0 .57em .17em 0;
  }
  &__price{
    font-size: 1.5em;
    line-height: 133%;
    font-weight: 700;
  }
  &__count{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
  &__btn{
    width: 100%;
  }
  .favourite-btn{
    ._view2{
      display: none;
    }
    &._added{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__available{
    margin-top: 1em;
    text-align: center;
    color: $green;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: .75em;
    &__top{
      gap: .25em;
    }
    &__grid{
      grid-template-columns: 20.25em calc(100% - 22.25em);
      gap: 2em;
      margin-top: 1em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .75em;
    &__big{
      height: 19.75em;
    }
    &__small{
      order: 2;
      height: auto;
      position: relative;
      .swiper{
        height: auto;
        width: calc(100% + .75em);
        &-slide{
          padding: 0 .75em 0 0;
          height: auto;
          width: 25%;
        }
      }
      .gallery__img{
        padding-bottom: 100%;
        height: unset;
      }
    }
    .swiper-button{
      width: 1.75em;
      height: 1.75em;
      &-prev{
        left: -.875em;
      }
      &-next{
        right: -.875em;
      }
    }
  }

  .catalog-info{
    grid-template-columns: 100%;
  }

  .catalog-props{
    padding: 0;
    max-width: 14em;
  }

  .catalog-colors{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
  }

  .catalog-cart{
    padding: 1.25em;
    &__nav,
    &__count{
      margin-top: 1em;
    }
    &__nav{
      gap: .5em;
    }
    &__btn{
      height: 2.75em;
      span{
        font-size: .875em;
      }
    }
    .cartcalc{
      grid-template-columns: 2.75em calc(100% - 5.5em) 2.75em;
      height: 2.75em;
      width: 9.125em;
      input{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    .h1{
      font-size: 1.375em;
    }
    &__code{
      font-size: .75em;
      line-height: 150%;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
