@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 3em;
    border-radius: .5em;
    font-weight: 500;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: $primary-hover;
        color: $white;
    }
    &:active{
        background-color: $primary;
        color: $white;
    }
    &:disabled{
        background-color: $light-grey;
        color: $pale-text;
    }
}

.m-btn-light{
    background-color: #edeafd;
    color: $primary;
    &:hover{
        background-color: #e5e2fa;
        color: $primary;
    }
    &:active{
        background-color: #edeafd;
        color: $primary;
    }
}

.m-btn-primary-outline{
    color: $primary;
    border: 1px solid $primary;
    &:hover{
        border-color: $primary-hover;
        color: $primary-hover;
    }
    &:active{
        border-color: $primary;
        color: $primary ;
    }
}
