.contacts{
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 25.75em calc(100% - 25.75em - 3.75em);
    gap: 3.75em;
    margin-top: 2.5em;
  }
  .socials__item{
    width: 3em;
  }
}

.contacts-item{
  @include grid100gap(.5em);
  padding-bottom: 1.5em;
  border-bottom: $border;
  margin-bottom: 1.5em;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 137%;
    font-weight: 500;
  }
  &__title{
    text-transform: uppercase;
    color: $light-text;
  }
  &__desc{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a{
      line-height: inherit;
    }
  }
}

.map{
  min-height: 100%;
  height: 34.75em;
  overflow: hidden;
  border-radius: 1em;
}

.contacts-gallery{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
  &__img{
    border-radius: 1em;
    @include imgCover(62.5%);
    display: block;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 20.25em calc(100% - 21.75em);
      gap: 1.5em;
    }
    .socials__item{
      width: 2.75em;
    }
  }

  .contacts-item{
    padding-bottom: 1em;
    margin-bottom: 1em;
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
  }

  .map{
    height: 17.5em;
  }

  .contacts-gallery{
    margin-top: 1.5em;
    .swiper{
      &-slide{
        width: 15.75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
