.order-thank{
  &__top{
    margin-top: 2.5em;
    @include grid100gap(1em);
  }
  &__subtitle{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 600;
  }
  &__desc{
    a{
      color: $primary;
      font-weight: 600;
      white-space: nowrap;
      &:hover{
        color: $pink;
      }
    }
  }
  &__pickup{
    margin-top: 2em;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.75em);
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__btn{
    padding: 0 2em;
    margin-right: 1.5em;
  }
}

.contacts-item2{
  display: flex;
  align-items: center;
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .75em;
  }
  &__text{
    font-size: 1em;
    line-height: 137%;
    font-weight: 500;
    a{
      white-space: nowrap;
    }
  }
}


@media screen and (max-width: 992px){
  .order-thank{
    .h1{
      font-size: 1.625em;
    }
    &__top{
      margin-top: 1.5em;
      gap: .75em;
    }
    &__pickup{
      margin-top: 1em;
    }
    &__items{
      margin-top: .75em;
      gap: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-thank{
    &__subtitle{
      font-size: 1em;
      line-height: 137%;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
    &__nav{
      @include grid100gap(.5em);
    }
    &__btn{
      margin: 0;
      span{
        font-size: .875em;
      }
    }
  }

  .contacts-item2{
    img{
      margin-right: .5em;
    }
    &__text{
      font-size: .875em;
      line-height: 143%;
    }
  }
}
