.cart{
  &._selected{
    .cart__delete{
      opacity: 1;
      pointer-events: unset;
    }
    .cart-detail{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 25.625em) 20.625em;
    gap: 1.5em 5em;
  }
  &__top{
    margin-top: 2.5em;
  }
  &__bottom{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__delete{
    font-weight: 500;
    color: $red;
    opacity: 0;
    pointer-events: none;
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  &__nav2{
    margin-top: 2.5em;
    display: flex;
  }
  &__back{
    display: flex;
    align-items: center;
    color: $primary;
    font-weight: 600;
    &:hover{
      color: $pink;
      path{
        stroke: $pink;
      }
    }
    svg{
      margin-right: .75em;
      width: 1.5em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.cart-card{
  padding: 1.5em 0;
  display: grid;
  grid-template-columns: 1.5em 8.75em calc(100% - 19.5em) 7em;
  gap: .75em;
  border-top: $border;
  align-items: flex-start;
  &:not(._multi){
    .cart-card__price{
      opacity: 0;
      pointer-events: none;
    }
  }
  &:last-child{
    border-bottom: $border;
  }
  &__img{
    @include imgCover(100%);
  }
  &__title{
    font-size: 1em;
    line-height: 137%;
    font-weight: 600;
  }
  &__price,
  &__code{
    font-size: .875em;
    line-height: 143%;
    color: $light-text;
  }
  &__code{
    margin-top: .57em;
  }
  &__price{
    margin-top: .285em;
  }
  &__total{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 700;
  }
  &__action{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
  }
  &__btn{
    width: 3em;
    height: 3em;
    @include center;
    background-color: transparent;
    svg{
      width: 1.5em;
    }
  }
  &__delete{
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
  &__check{
    align-self: center;
  }
  @media screen and (min-width: 577px){
    &__check{
      grid-row: 1/3;
    }
    &__img{
      grid-row: 1/3;
      grid-column: 2/3;
    }
    &__calc{
      grid-row: 2/3;
      grid-column: 3/4;
      display: flex;
      align-items: flex-end;
    }
    &__nums{
      grid-column: 4/5;
      grid-row: 1/2;
      text-align: right;
    }
    &__info,
    &__calc{
      padding-left: 1.25em;
    }
  }
}

.cart-detail{
  border-radius: 1em;
  padding: 1.5em;
  background-color: $white;
  box-shadow: $shadow;
  top: 1em;
  position: sticky;
  &._view2{
    display: none;
  }
  &__desc,
  &__title,
  &__link{
    font-size: 1em;
    line-height: 137%;
  }
  &__title{
    font-weight: 600;
  }
  &__btn{
    width: 100%;
    margin-top: 1.5em;
  }
  &__title2{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 700;
  }
  &__desc{
    margin-top: 1em;
    font-weight: 500;
    color: $light-text;
  }
  &__sum{
    padding-top: 1.5em;
    margin-top: 1.5em;
    border-top: $border;
    display: flex;
    justify-content: space-between;
    &-title,
    &-num{
      font-size: 1.5em;
      line-height: 133%;
      font-weight: 700;
    }
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    margin-top: 1em;
    .cart-detail__desc{
      margin: 0;
    }
  }
  &__link{
    font-weight: 500;
    color: $primary;
    &:hover{
      color: $pink;
    }
  }
  &__items{
    margin-top: .25em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .75em);
  }
  &__item{
    @include imgCover(100%);
    margin: .75em .75em 0 0;
    width: 5em;
    height: 5em;
    padding: 0;
    border-radius: .5em;
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__grid{
      grid-template-columns: 100%;
    }
    &__top,
    &__nav2{
      margin-top: 1.5em;
    }
  }

  .cart-card{
    padding: 1em 0;
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__back{
      svg{
        margin-right: .5em;
        width: 1.25em;
      }
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .cart-card{
    align-items: center;
    grid-template-columns: 1.5em 6.25em calc(100% - 8.75em);
    gap: 1em .5em;
    &__title{
      font-size: .875em;
      line-height: 143%;
    }
    &__code{
      font-size: .75em;
      line-height: 150%;
      margin-top: .67em;
    }
    &__calc{
      grid-column: 1/3;
    }
    .cartcalc{
      grid-template-columns: 2.75em calc(100% - 5.5em) 2.75em;
      height: 2.75em;
      width: 9.125em;
      input{
        font-size: .875em;
      }
    }
    &__nums{
      grid-column: 1/-1;
      display: flex;
      align-items: center;
    }
    &__price{
      margin: 0 0 0 1.14em;
    }
    &__btn{
      width: 2.75em;
      height: 2.75em;
    }
  }

  .cart-detail{
    padding: 1.25em;
    &__btn{
      margin-top: 1em;
    }
    &__desc{
      margin-top: .75em;
    }
    &__sum{
      padding-top: 1em;
      &-title,
      &-num{
        font-size: 1.25em;
        line-height: 140%;
      }
    }
  }
}

