.header-top {
  padding: .625em 0;
  background-color: $light-grey;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav{
  &>ul{
    display: flex;
    align-items: center;
    &>li{
      @include lastM(0 1.4375em 0 0);
      &>a{
        font-size: .875em;
        line-height: 143%;
        font-weight: 600;
      }
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.header-bottom{
  padding: .625em 0;
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  box-shadow: $shadow;
  background-color: #fff;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__contact{
    min-height: 3.375em;
    margin-left: 1.5em;
    padding-left: 1.25em;
    display: flex;
    align-items: center;
    border-left: $border;
    &-link{
      color: $pink;
      white-space: nowrap;
    }
  }
  &._open-catalog{
    .catalog-btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .catalog-menu{
      pointer-events: unset;
      opacity: 1;
    }
  }
}

.burger{
  display: none;
}

.catalog-btn{
  font-weight: 600;
  margin-left: 2em;
  &__icon{
    width: 1.5em;
    height: 1.5em;
    display: block;
    flex-shrink: 0;
    margin-right: .5em;
    position: relative;
    img{
      width: 100%;
      transition: all .3s;
      @include centerAbsolute;
    }
  }
  &__title{
    font-size: .875em;
  }
  ._view2{
    opacity: 0;
  }
}

.header-search{
  margin: 0 1.25em 0 1.5em;
  flex-grow: 1;
}

.m-search{
  position: relative;
  &._show-result{
    .m-search__dropdown,
    .m-search__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    input{
      font-size: .875em;
      height: 3.43em;
      padding: 0 6.5em 0 1.43em;
    }
  }
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 3em;
    border-radius: 0 .5em .5em 0;
    img{
      width: 1.5em;
    }
  }
  &__clear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 3.625em;
    width: 1.5em;
    @include center;
    opacity: 0;
    pointer-events: none;
    svg{
      width: 100%;
    }
    &:hover{
      line{
        stroke: $red;
      }
    }
  }
  &__dropdown{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 1.25em;
    box-shadow: $shadow;
    background-color: #fff;
    z-index: 10;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    max-height: 25em;
    overflow-y: auto;
  }
}

.search-result{
  @include grid100gap(1.75em);
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    font-size: .875em;
    line-height: 143%;
    margin-top: 1.43em;
    span{
      font-weight: 600;
    }
    &:first-child{
      margin-top: 0;
    }
  }
}

.menu-contacts{
  display: none;
}

._pin{
  padding: 0 .5625em;
  background-color: $red;
  height: 1.5em;
  min-width: 1.5em;
  @include center;
  border-radius: 6.25em;
  font-weight: 600;
  color: #fff;
}

.mob-menu{
  display: flex;
  ._pin{
    position: absolute;
    top: -.625em;
    left: 50%;
    transform: translateX(.875em);
  }
  &__link{
    font-weight: 500;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 5.25em;
    position: relative;
    svg{
      height: 1.5em;
      width: auto;
      margin-bottom: .125em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  @media screen and (min-width: 993px){
    flex-direction: row-reverse;
    &__link{
      margin-left: .75em;
      &--hide-tablet{
        display: none;
      }
      path{
        stroke: $black;
      }
    }
  }
  &__title{
    font-size: .875em;
    line-height: 143%;
  }
}

.catalog-menu{
  &__show{
    display: none;
  }
  @media screen and (min-width: 993px){
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -.5em;
    max-width: 1472px;
    width: 100%;
    border-radius: 1.5em;
    background-color: $white;
    box-shadow: $shadow;
    padding: 2.5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    .catalog-menu__hidden{
      display: block !important;
    }
  }
  ul{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1.5em;
  }
  li{
    display: flex;
  }
  a{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 1em;
    line-height: 125%;
  }
  img{
    width: 3.5em;
    height: 3.5em;
    border-radius: .5em;
    flex-shrink: 0;
    margin-right: 1em;
  }
}


@media screen and (max-width: 992px){
  .header-top{
    background-color: transparent;
    padding: 0;
    &__desc{
      display: none;
    }
  }

  .header-bottom{
    padding: .5em 0 .875em;
    &__inner{
      display: block;
    }
    &__left{
      z-index: 11;
      position: relative;
    }
  }

  .menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    z-index: 10;
    padding: 8.375em 1.125em 1.25em;
  }

  .burger{
    position: relative;
    width: 2.5em;
    height: 2.5em;
    display: flex;
    margin-left: auto;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
    }
  }

  .header{
    box-shadow: $shadowMob;
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        pointer-events: unset;
        opacity: 1;
      }
    }
    &__logo{
      width: 4em;
    }
    &__contact{
      margin-left: .75em;
      padding-left: .75em;
      min-height: 2.125em;
      &-link{
        font-size: 1em;
      }
    }
  }

  .catalog-btn{
    display: none;
  }

  .header-search{
    margin: .875em 0 0;
    width: 100%;
    font-size: .92em;
    z-index: 11;
  }

  .nav{
    &>ul{
      flex-direction: column;
      align-items: flex-start;
      &>li{
        @include lastM(0 0 1.71em);
        &>a{
          font-weight: 500;
        }
      }
    }
  }

  .menu-contacts{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border;
    display: block;
    &__street{
      max-width: 15.5em;
    }
    .socials{
      margin-top: 1em;
    }
  }

  ._pin{
    height: 1.25em;
    min-width: 1.25em;
    padding: 0 .375em;
    .text12{
      font-size: .6875em;
    }
  }

  .mob-menu{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    justify-content: center;
    color: $light-text;
    padding: .5em 1em;
    box-shadow: $shadowMob;
    z-index: 9;
    background-color: $white;
    &__title{
      font-size: .75em;
      line-height: 150%;
    }
    ._pin{
      transform: translateX(.375em);
    }
    &__link{
      &--active{
        color: $primary;
        path{
          stroke: $primary;
        }
      }
    }
  }

  .catalog-menu{
    margin-bottom: 1.5em;
    &._open{
      .catalog-menu__show img{
        transform: rotate(-180deg);
      }
    }
    &__show{
      display: flex;
      align-items: center;
      font-weight: 700;
      text-transform: uppercase;
      color: $primary;
      img{
        margin: 0 0 0 .5em;
        width: 1em;
        height: auto;
        transition: all .3s;
      }
    }
    &__hidden{
      display: none;
    }
    ul{
      padding: 1em 0 0 1em;
      grid-template-columns: 100%;
      gap: 1em;
      img{
        display: none;
      }
    }
    a{
      font-size: .875em;
      line-height: 143%;
      font-weight: 400;
    }
  }
}


