.pagination{
  display: flex;
  align-items: center;
  margin-left: -.25em;
  li:not(.prev):not(.next){
    margin: 0 .25em;
    a{
      height: 2.86em;
      min-width: 2.86em;
      padding: 0 .875em;
      border-radius: .57em;
      border: $border;
      @include center;
      font-weight: 500;
      font-size: .875em;
      line-height: 143%;
      color: $light-text;
    }
    svg{
      width: 1.0625em;
      height: auto;
    }
    &.active{
      pointer-events: none;
    }
    &:hover,
    &.active{
      a{
        color: $white;
        background-color: $primary;
        border-color: $primary;
      }
    }
  }
  .active{

  }
  a{
    @include center;
    width: 100%;
    height: 100%;
  }
  .next,
  .prev{
    width: 1.5em;
    margin: 0 .25em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary-hover;
      }
    }
  }
}

.pagination2{
  display: flex;
  align-items: center;
  &__text{
    color: $light-text;
  }
  .m-select{
    margin: 0 .75em 0 1em;
    .current{
      @include sameNumWidth;
    }
  }
}

