.order{
  .cart__bottom{
    margin-top: 2.5em;
  }
  &:not(._filled){
    ._make-order{
      background-color: $light-grey;
      color: $pale-text;
    }
  }
  &:not(._show-address){
    ._address{
      display: none;
    }
  }
}

.order-form{
  max-width: 42.75em;
  width: 100%;
  @include grid100gap(2.5em);
  &__title{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 600;
  }
  &__fields{
    margin-top: 1em;
  }
  ._full{
    grid-column: 1/-1;
  }
}

.radio-field{
  font-weight: 500;
  input:checked~.radio-field__inner{
    background-color: #fff;
    box-shadow: $shadow;
    &:before{
      border-width: .375em;
      border-color: $primary;
    }
  }
  &__inner{
    padding: 1.25em 1.375em;
    display: flex;
    align-items: center;
    border-radius: .5em;
    background-color: $light-grey;
    min-height: 3.75em;
    transition: all .3s;
    &:before{
      content: '';
      width: 1.25em;
      height: 1.25em;
      flex-shrink: 0;
      border: 2px solid $input-fields;
      border-radius: 50%;
      margin-right: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .order{
    .cart__bottom{
      margin-top: 1.5em;
    }
  }

  .order-form{
    max-width: 100%;
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-form{
    gap: 1.5em;
    &__fields{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 1em;
      line-height: 137%;
    }
  }

  .radio-field{
    &__inner{
      padding: .625em 1.125em;
      min-height: 3.375em;
    }
    .text16{
      font-size: .875em;
    }
  }
}
