.collection{
  padding: 2em 0;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: $primary;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    svg{
      width: 1.5625em;
      margin-left: .5em;
    }
    &:hover{
      color: $pink;
      path{
        stroke: $pink;
      }
    }
  }
}

.multi-slider{
  .swiper {
    &-wrapper {
      align-items: stretch;
    }
    &-container {
      width: calc(100% + 1.5em);
    }
    &-slide{
      height: auto;
      padding-right: 1.5em;
    }
  }
}

.collection-slider{
  margin-top: 2.5em;
  .swiper {
    &-slide{
      width: 25%;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
        pointer-events: unset;
      }
      @media screen and (min-width: 577px){
        &-next+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
      @media screen and (min-width: 577px){
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.product-card{
  height: 100%;
  padding: 1.5em;
  border-radius: 1em;
  box-shadow: $shadow;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &._added{
    .product-card__add{
      display: none;
    }
    .cartcalc{
      display: grid;
    }
  }
  &._disabled{
    pointer-events: none;
    color: $light-text;
    .product-card__img{
      opacity: 0.5;
    }
    .product-card__price{
      color: $pale-text;
    }
    .product-card__available{
      color: $light-text;
    }
    .product-card__add{
      color: $pale-text;
      background-color: $light-grey;
    }
    .favourite-btn{
      background-color: $light-grey;
      path{
        stroke: $pale-text;
      }
    }
  }
  &:hover{
    box-shadow: $shadow2;
  }
  &__img{
    @include imgCover(86.5%);
    margin-bottom: 1em;
  }
  &__title{
    font-size: 1em;
    line-height: 137%;
    font-weight: 600;
  }
  &__bottom{
    margin-top: auto;
    padding-top: .75em;
  }
  &__wrap{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__price{
    font-size: 1.5em;
    line-height: 133%;
    font-weight: 700;
  }
  &__available{
    font-size: .875em;
    line-height: 143%;
    font-weight: 500;
    color: $green;
  }
  &__nav{
    display: flex;
    margin-top: .75em;
  }
  &__cart{
    flex-grow: 1;
  }
  &__add{
    width: 100%;
  }
  .cartcalc{
    display: none;
    width: 100%;
  }
  .favourite-btn{
    margin-left: .5em;
  }
}


@media screen and (max-width: 992px){
  .collection{
    padding: .75em 0;
  }

  .multi-slider{
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        padding-right: .75em;
      }
    }
  }

  .collection-slider{
    margin-top: 1em;
  }

  .product-card{
    padding: .75em;
    &._added{
      .favourite-btn{
        display: none;
      }
    }
    &__img{
      margin-bottom: .5em;
    }
    &__title{
      font-size: .75em;
      line-height: 150%;
    }
    &__price{
      font-size: .875em;
      line-height: 143%;
    }
    &__available{
      font-size: .6875em;
      line-height: 164%;
    }
    &__bottom{
      padding-top: .25em;
    }
    &__nav{
      margin-top: .5em;
    }
    &__add{
      height: 2.5em;
      padding: 0 .5em;
      span{
        font-size: .75em;
      }
    }
    .favourite-btn{
      width: 2.5em;
      height: 2.5em;
      margin-left: .25em;
      svg{
        width: 1.25em;
      }
    }
    .cartcalc{
      grid-template-columns: 2.5em calc(100% - 5em) 2.5em;
      height: 2.5em;
      input{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .collection{
    &__inner{
      position: relative;
      padding-bottom: 3.75em;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      justify-content: center;
      border-radius: .5em;
      height: 2.75em;
      border: $border;
      span{
        font-size: .875em;
      }
      svg{
        width: 1.25em;
      }
    }
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}
